import React, { Component } from "react";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-enterprise";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-alpine.css";
import "../assets/css/HomeReport.css";

class DivisionReport extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columnDefs: [
        {
          headerName: "Toastmasters Division",
          field: "division",
          sort: "asc",
          chartDataType: "category",
        },
        {
          headerName: "All 7 Officers",
          field: "allseven",
          chartDataType: "series",
          width: 100,
        },
        {
          headerName: "Four or More",
          field: "fourormore",
          chartDataType: "series",
          width: 100,
        },
        {
          headerName: "Clubs with at least One",
          field: "atleastone",
          chartDataType: "series",
        },
        {
          headerName: "Four or More / Total Clubs (%)",
          colId: "registration-percent",
          chartDataType: "series",
          valueGetter: function (params) {
            return (
              Math.round(
                ((params.getValue("fourormore") * 7) /
                  params.getValue("signuptotal")) *
                  100
              ) + "%"
            );
          },
        },
        {
          headerName: "Clubs with at least One / Total Clubs (%)",
          colId: "registration-percent",
          chartDataType: "series",
          valueGetter: function (params) {
            return (
              Math.round(
                ((params.getValue("atleastone") * 7) /
                  params.getValue("signuptotal")) *
                  100
              ) + "%"
            );
          },
        },
        {
          headerName: "Training Verified",
          field: "verified",
          chartDataType: "series",
          hide: true,
        },
        {
          headerName: "Registered But Not Confirmed",
          field: "registered",
          chartDataType: "series",
          hide: true,
        },
        {
          headerName: "Not Registered",
          field: "missing",
          chartDataType: "series",
          hide: true,
        },
        {
          headerName: "Total",
          field: "signuptotal",
          chartDataType: "series",
          hide: true,
        },
      ],
      defaultColDef: {
        sortable: true,
        filter: true,
        resizable: true,
        width: 150,
      },
      popupParent: document.body,
      onGridReady: this.onFirstDataRendered,
    };
  }

  onFirstDataRendered(params) {
    var defaultSortModel = [{ colId: "division", sort: "asc" }];
    params.api.setSortModel(defaultSortModel);
    params.api.resetRowHeights();
    params.api.sizeColumnsToFit();
  }

  componentDidMount() {
    fetch(
      "https://a5slwb8wx6.execute-api.us-east-1.amazonaws.com/dev/list-district-data",
      { retries: 4, retryDelay: 1000 }
    )
      .then((resp) => resp.json())
      .then((data) => {
        var dataByClub = data.reduce((dataByClub, singleRow) => {
          const group = singleRow.clubName;
          if (!dataByClub[group]) {
            dataByClub[group] = {
              division: singleRow.division,
              area: singleRow.area,
              clubName: singleRow.clubName,
              signuptotal: 0,
              missing: 0,
              verified: 0,
              registered: 0
            };
          }
          if (singleRow.verified) {
            dataByClub[group]["verified"] =
              (dataByClub[group]["verified"] || 0) + 1;
          }
          if (singleRow.registered) {
            dataByClub[group]["registered"] =
              (dataByClub[group]["registered"] || 0) + 1;
          }
          dataByClub[group]["signuptotal"] =
            (dataByClub[group]["signuptotal"] || 0) + 1;
          dataByClub[group]["missing"] =
            dataByClub[group]["signuptotal"] -
            ((dataByClub[group]["registered"] || 0) +
              (dataByClub[group]["verified"] || 0));
          const totalSignups = (dataByClub[group]["verified"] || 0) + (dataByClub[group]["registered"] || 0);
          dataByClub[group]["atleastone"] = totalSignups > 0;
          dataByClub[group]["fourormore"] = totalSignups > 3;
          dataByClub[group]["allseven"] = totalSignups === 7;
          return dataByClub;
        }, {});

        const dataByDivision = Object.keys(dataByClub)
          .map((db) => dataByClub[db])
          .reduce(function (dataByDivision, singleRow) {
            const group = singleRow["division"];

            if (!dataByDivision[group]) {
              dataByDivision[group] = {
                division: group,
              };
            }

            dataByDivision[group]["verified"] =
              (dataByDivision[group]["verified"] || 0) + singleRow["verified"];
            dataByDivision[group]["registered"] =
              (dataByDivision[group]["registered"] || 0) +
              singleRow["registered"];
            dataByDivision[group]["missing"] =
              (dataByDivision[group]["missing"] || 0) + singleRow["missing"];
            dataByDivision[group]["signuptotal"] =
              (dataByDivision[group]["signuptotal"] || 0) +
              singleRow["signuptotal"];

            dataByDivision[group]["atleastone"] =
              (dataByDivision[group]["atleastone"] || 0) +
              singleRow["atleastone"];
            dataByDivision[group]["fourormore"] =
              (dataByDivision[group]["fourormore"] || 0) +
              singleRow["fourormore"];
            dataByDivision[group]["allseven"] =
              (dataByDivision[group]["allseven"] || 0) + singleRow["allseven"];

            return dataByDivision;
          }, {});
        const divisionRows = Object.keys(dataByDivision).map(
          (group) => dataByDivision[group]
        );

        const totalRow = divisionRows.reduce((totalRow, d) => {
          totalRow["division"] = ":Total:";
          totalRow["verified"] = (totalRow["verified"] || 0) + d["verified"];
          totalRow["registered"] =
            (totalRow["registered"] || 0) + d["registered"];
          totalRow["missing"] = (totalRow["missing"] || 0) + d["missing"];
          totalRow["signuptotal"] =
            (totalRow["signuptotal"] || 0) + d["signuptotal"];
          totalRow["atleastone"] =
            (totalRow["atleastone"] || 0) + d["atleastone"];
          totalRow["fourormore"] =
            (totalRow["fourormore"] || 0) + d["fourormore"];
          totalRow["allseven"] = (totalRow["allseven"] || 0) + d["allseven"];
          return totalRow;
        }, {});

        return Array.prototype.concat(divisionRows, totalRow);
      })
      .then((rowData) => this.setState({ rowData }))
      .catch(function (error) {
        // if there's an error, log it
        console.log(error);
      });
  }

  getHeight() {
    return 320 + "px";
  }

  createChartContainer = (chartRef) => {
    if (currentChartRef) {
      currentChartRef.destroyChart();
    }
    var eChart = chartRef.chartElement;
    var eParent = document.querySelector("#myChart");
    eParent.appendChild(eChart);
    currentChartRef = chartRef;
  };

  render() {
    return (
      <div
        className="content ag-theme-alpine"
        style={{
          height: this.getHeight(),
          width: "100%",
        }}
      >
        <AgGridReact
          columnDefs={this.state.columnDefs}
          defaultColDef={this.state.defaultColDef}
          popupParent={this.state.popupParent}
          rowData={this.state.rowData}
          enableRangeSelection={true}
          enableCharts={true}
          processChartOptions={this.state.processChartOptions}
          createChartContainer={this.createChartContainer}
          onGridReady={this.onFirstDataRendered.bind(this)}
        />
      </div>
    );
  }
}
var currentChartRef;
export default DivisionReport;
