import React, { Component } from "react";
import "./App.css";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-alpine.css";
import "ag-grid-enterprise";
const clubData = require("variables/clubData");

class VerifiedOnly extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columnDefs: [
        {
          headerName: "Division",
          field: "division",
          sort: "asc",
          chartDataType: "category",
        },
        {
          headerName: "Area",
          field: "area",
          sort: "asc",
          chartDataType: "category",
        },
        {
          headerName: "Club Name",
          field: "clubName",
          sortable: true,
        },
        {
          headerName: "Club Number",
          field: "clubNumber",
          sortable: true,
        },
        {
          headerName: "Dashboard Show",
          field: "trained",
          sortable: true,
        },
        {
          headerName: "TLI Records Show",
          field: "verified",
          sortable: true,
        },
        {
          headerName: "Needs Update",
          valueGetter: function (params) {
            return params.getValue("verified") - params.getValue("trained");
          },
          sortable: true,
        },
        {
          headerName: "Pres",
          field: "President",
        },
        {
          headerName: "VPE",
          field: "VicePresidentEducation",
        },
        {
          headerName: "VPM",
          field: "VicePresidentMembership",
        },
        {
          headerName: "VPPR",
          field: "VicePresidentPublicRelations",
        },
        {
          headerName: "Secr",
          field: "Secretary",
        },
        {
          headerName: "Treas",
          field: "Treasurer",
        },
        {
          headerName: "SAA",
          field: "SergeantAtArms",
        },
      ],
      statusBar: {
        statusPanels: [
          { statusPanel: "agTotalAndFilteredRowCountComponent", align: "left" },
        ],
      },
      defaultColDef: {
        sortable: true,
        filter: true,
        resizable: true,
      },
      autoGroupColumnDef: {
        cellRendererParams: {
          suppressCount: true,
        },
      },
      groupMultiAutoColumn: true,
      popupParent: document.body,
      groupIncludeFooter: true,
      sideBar: false,
      enableRangeSelection: true,
      enableCharts: true,
      onGridReady: this.onFirstDataRendered,
      processChartOptions: this.processChartOptions,
    };
  }

  processChartOptions(params) {
    var options = params.options;

    // console.log('chart options:', options);

    // we are only interested in processing bar type.
    // so if user changes the type using the chart control,
    // we ignore it.
    if (
      [
        "stackedBar",
        "groupedBar",
        "normalizedBar",
        "stackedColumn",
        "groupedColumn",
        "normalizedColumn",
      ].indexOf(params.type) < 0
    ) {
      console.log("chart type is " + params.type + ", making no changes.");
      return options;
    }

    options.seriesDefaults.fill.colors = ["#e1ba00", "silver", "peru"];
    options.seriesDefaults.fill.opacity = 0.8;

    options.seriesDefaults.stroke.colors = ["black", "#ff0000"];
    options.seriesDefaults.stroke.opacity = 0.8;
    options.seriesDefaults.stroke.width = 2;

    options.seriesDefaults.shadow.enabled = true;
    options.seriesDefaults.shadow.color = "rgba(0, 0, 0, 0.3)";
    options.seriesDefaults.shadow.xOffset = 10;
    options.seriesDefaults.shadow.yOffset = 5;
    options.seriesDefaults.shadow.blur = 8;

    options.seriesDefaults.label.enabled = true;
    options.seriesDefaults.label.fontStyle = "italic";
    options.seriesDefaults.label.fontWeight = "bold";
    options.seriesDefaults.label.fontSize = 15;
    options.seriesDefaults.label.fontFamily = "Arial, sans-serif";
    options.seriesDefaults.label.color = "green";
    options.seriesDefaults.label.formatter = function (params) {
      return "<" + params.value + ">";
    };

    options.seriesDefaults.highlightStyle.fill = "red";
    options.seriesDefaults.highlightStyle.stroke = "yellow";

    options.seriesDefaults.tooltip.renderer = function (params) {
      var x = params.datum[params.xKey];
      var y = params.datum[params.yKey];
      return (
        '<u style="color: ' +
        params.color +
        '">' +
        (params.title || params.yName) +
        "</u><br><br><b>" +
        params.xName.toUpperCase() +
        ":</b> " +
        x +
        "<br/><b>" +
        params.yName.toUpperCase() +
        ":</b> " +
        y
      );
    };

    return options;
  }

  onFirstDataRendered(params) {
    var defaultSortModel = [
      { colId: "division", sort: "asc" },
      { colId: "area", sort: "asc" },
      { colId: "clubName", sort: "asc" },
    ];
    params.api.setSortModel(defaultSortModel);
    params.api.resetRowHeights();
    params.api.sizeColumnsToFit();
  }

  async componentDidMount() {
    fetch(
      "https://a5slwb8wx6.execute-api.us-east-1.amazonaws.com/dev/list-district-data",
      { retries: 4, retryDelay: 1000 }
    )
      .then((resp) => resp.json())
      .then(async (data) => {
        var dataByClub = data.reduce((dataByClub, singleRow) => {
          const group = singleRow.clubId;
          if (!dataByClub[group]) {
            dataByClub[group] = {
              division: singleRow.division,
              area: singleRow.area,
              clubName: singleRow.clubName,
              clubNumber: singleRow.clubId,
              signuptotal: 0,
              missing: 0,
              verified: 0,
              registered: 0,
            };
          }
          if (singleRow.verified) {
            dataByClub[group]["verified"] =
              (dataByClub[group]["verified"] || 0) + 1;
            dataByClub[group][singleRow.office] = 1;
          }
          if (singleRow.registered) {
            dataByClub[group]["registered"] =
              (dataByClub[group]["registered"] || 0) + 1;
          }
          dataByClub[group]["signuptotal"] =
            (dataByClub[group]["signuptotal"] || 0) + 1;
          dataByClub[group]["missing"] =
            dataByClub[group]["signuptotal"] -
            ((dataByClub[group]["registered"] || 0) +
              (dataByClub[group]["verified"] || 0));
          const totalSignups =
            (dataByClub[group]["verified"] || 0) +
            (dataByClub[group]["registered"] || 0);
          dataByClub[group]["atleastone"] = totalSignups > 0;
          dataByClub[group]["fourormore"] = totalSignups > 3;
          dataByClub[group]["allseven"] = totalSignups === 7;
          return dataByClub;
        }, {});

        const allClubs = await clubData.getLiveStaticClubData();
        allClubs.forEach((club) => {
          const group = club.clubNumber;
          if (!(group in dataByClub)) {
            dataByClub[group] = {
              division: club["division"],
              area: club["area"],
              clubName: club["clubName"],
              clubNumber: club.clubNumber,
              signups: "0/7",
              verifiedfraction: "0/7",
              missing: 7,
              verified: 0,
              registered: 0,
              signuptotal: 7,
            };
          }
          // handle re-districting
          dataByClub[group]["clubName"] = club["clubName"];
          dataByClub[group]["area"] = club["Area"];
          dataByClub[group]["division"] = club["Division"];
          dataByClub[group]["trained"] = club.trained;
        });

        return Object.keys(dataByClub).map(function (group) {
          return dataByClub[group];
        });
      })
      .then((rowData) => this.setState({ rowData }))
      .catch(function (error) {
        // if there's an error, log it
        console.log(error);
      });
  }

  getHeight() {
    return window.innerHeight + "px";
  }

  render() {
    return (
      <div
        className="content ag-theme-alpine"
        style={{
          height: this.getHeight(),
          width: "100%",
        }}
      >
        <AgGridReact
          columnDefs={this.state.columnDefs}
          defaultColDef={this.state.defaultColDef}
          groupIncludeFooter={this.state.groupIncludeFooter}
          sideBar={this.state.sideBar}
          pivotMode={this.state.pivotMode}
          autoGroupColumnDef={this.state.autoGroupColumnDef}
          rowData={this.state.rowData}
          statusBar={this.state.statusBar}
          popupParent={this.state.popupParent}
          enableRangeSelection={this.state.enableRangeSelection}
          enableCharts={this.state.enableCharts}
          groupMultiAutoColumn={this.state.groupMultiAutoColumn}
          onGridReady={this.state.onGridReady}
          rowGroupPanelShow="always"
        ></AgGridReact>
      </div>
    );
  }
}

export default VerifiedOnly;
