export async function getLiveStaticClubData() {
  const response = await fetch('https://a5slwb8wx6.execute-api.us-east-1.amazonaws.com/dev/clubs', {retries: 4,retryDelay: 1000}); 
  const records = await response.json();
  return records.filter(c => c["Club Status"] === "Active").map(c => {
    let r = c;
    r.clubName = r["Club Name"];
    r.clubNumber = parseInt(r["Club Number"]);
    r.trained = parseInt(r["Off. Trained Round 2"]);
    return r;
  });
}

export function getClubNumber(clubName) {
  const firstParens = clubName.lastIndexOf('(');
  const lastParens = clubName.lastIndexOf(')');
  const result = parseInt(clubName.substring(firstParens + 1, lastParens));
  // This is a misentered row, that needs to be patched
  if (result === 77665933) { return 7766593; }
  return result;
}

export function filterInvalid(s) {
  let valid = s["clubName"] !== "g-Toastmasters (5589856)";
  return valid;
}

export function getConfirmedClubAttendees(data) {
    var dataByOfficer = data.filter(s => s["division"] !== "Outside District 46").filter(s => this.filterInvalid(s))
    .filter(s => s["checked_in"] || new Date().getTime() < new Date(s["startTime"]).getTime())
    .reduce(
      function (dataByOfficer, singleRow) {
        let value = singleRow["first_name"];
        const role = singleRow["role"]
        const group = singleRow["clubName"] + role;

        if (!dataByOfficer[group] || !dataByOfficer[group]["checked_in"]){
            dataByOfficer[group] = {
                "division": singleRow["division"],
                "area": singleRow["area"],
                "clubName": singleRow["clubName"],
                "role": role,
                "startTime": singleRow["startTime"],
                "first_name": value,
                "checked_in" : singleRow["checked_in"]
            };
        }

        return dataByOfficer;
    }, {});
    
    const allClubs = this.getLatestStaticClubData();
    allClubs.forEach(club => {
      this.backFillOfficerRoles(club, "President", dataByOfficer);
      this.backFillOfficerRoles(club, "Vice President of Education", dataByOfficer);
      this.backFillOfficerRoles(club, "Secretary", dataByOfficer);
      this.backFillOfficerRoles(club, "Vice President of Membership", dataByOfficer);
      this.backFillOfficerRoles(club, "Vice President of PR", dataByOfficer);
      this.backFillOfficerRoles(club, "Treasurer", dataByOfficer);
      this.backFillOfficerRoles(club, "Sergeant at Arms", dataByOfficer);
    });

    Object.keys(dataByOfficer).forEach(group => {
        let club = dataByOfficer[group];
        this.backFillOfficerRoles(club, "President", dataByOfficer);
        this.backFillOfficerRoles(club, "Vice President of Education", dataByOfficer);
        this.backFillOfficerRoles(club, "Secretary", dataByOfficer);
        this.backFillOfficerRoles(club, "Vice President of Membership", dataByOfficer);
        this.backFillOfficerRoles(club, "Vice President of PR", dataByOfficer);
        this.backFillOfficerRoles(club, "Treasurer", dataByOfficer);
        this.backFillOfficerRoles(club, "Sergeant at Arms", dataByOfficer);
      });

    return Object.keys(dataByOfficer).map(function(group){
        if (dataByOfficer[group]["checked_in"]) {
          dataByOfficer[group]["status"] = "Attended";
        }
        else if (dataByOfficer[group]["checked_in"] === false) {
          dataByOfficer[group]["status"] = "Future Date";
        }
        else {
          dataByOfficer[group]["status"] = "Never Registered";
        }

        return dataByOfficer[group];
    });
}

export function backFillOfficerRoles(club, role, dataByOfficer) {
    let group = club["clubName"] + role;
    if (!(group in dataByOfficer)){
        dataByOfficer[group] = {
          "division" : club["division"],
          "area" : club["area"],
          "clubName" : club["clubName"],
          "role": role
      };
    }
    dataByOfficer[group]["area"] = club["area"];
    dataByOfficer[group]["division"] = club["division"];
}